 *
 * Markup dynamically set in base.js:
 *   <div class="progress [progress--large][progress--small]"></div>
 */
@keyframes progress {
  to {
    transform: rotate(360deg);
  }
}

.progress {
  position: relative;
  min-width: 50px;
  min-height: 50px;
}

.progress::before {
  content: 'Loading...';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-top: -13px;
  /* width/2 + 1 */
  margin-left: -13px;
  /* width/2 + 1 */
}

.progress:not(:required)::before {
  content: '';
  border-radius: 50%;
  border-style: solid;
  border-color: #fff;
  border-top-color: #f60;
  animation: progress 0.6s linear infinite;
}

.progress--large::before {
  width: 48px;
  height: 48px;
  margin-top: -25px;
  /* width/2 + 1 */
  margin-left: -25px;
  /* width/2 + 1 */
}

.progress--large:not(:required)::before {
  border-width: 6px;
}

.progress--small::before {
  width: 16px;
  height: 16px;
  margin-top: -9px;
  /* width/2 + 1 */
  margin-left: -9px;
  /* width/2 + 1 */
}

.progress--small:not(:required)::before {
  border-width: 2px;
}

